import { Component } from '@angular/core';

@Component({
  selector: 'app-lect',
  templateUrl: './lect.component.html',
  styleUrls: ['./lect.component.scss']
})
export class LectComponent {
  page: string = "data"
  data: any;
  openData() {
    this.data = undefined;
    this.page = "data";
  }
  openNew(a) {
    if (a != null)
      this.data = { ...a };
    this.page = "newClient"
  }
}
