import { Pipe, PipeTransform } from '@angular/core';
import { lect } from './config.lect';

@Pipe({
  name: 'sort'
})
export class SortPipe implements PipeTransform {
  transform(value: lect[], ...sortArray: string[]) {
    let data = [];
    if (sortArray[0]) {
      value.forEach((element: lect) => {
        if (element.save) data.push(element);
      });
      return data;
    } else {
      value.forEach((element: lect) => {
        if (!element.save) data.push(element);
      });
      return data;
    };
  }
}
