import { Component, EventEmitter, OnInit, Output } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { lect } from '../config.lect';

@Component({
  selector: 'app-data-lect',
  templateUrl: './data-lect.component.html',
  styleUrls: ['./data-lect.component.scss']
})
export class DataLectComponent implements OnInit {
  @Output() openNew: EventEmitter<lect | null> = new EventEmitter();
  appData: lect[] = [];
  saveLec: boolean = false;
  searchText: string;
  constructor(public app: AppService) { }
  ngOnInit() {
    this.getClientsData();
  }
  deleteLect(data: lect) {
    this.app.dialogService.confirm(
      "تاكيد حذف الجلسة",
      "هل انت متاكد من حذف الجلسة",
      "نعم",
      "لا").subscribe(
        res => {
          if (res == true) {
            this.app.http.delete(`/api/lects/${this.app.user.id}/${data.clientId}/${data.id}`).subscribe(
              res => {
                this.ngOnInit();
              },
              err => {
                this.app.wait(false);
              }
            )
          }
        }
      )
  }
  getClientsData() {
    this.app.wait();
    this.app.http.get<lect[]>(`/api/lects/${this.app.user.id}`).subscribe(
      res => {
        this.appData = res;
        this.app.wait(false);
      }, err => { });
  }
}
