import { Component, EventEmitter, Input, OnInit, Output } from '@angular/core';
import { AppService } from 'src/app/app.service';
import { lect } from '../config.lect';

@Component({
  selector: 'app-new-lect',
  templateUrl: './new-lect.component.html',
  styleUrls: ['./new-lect.component.scss']
})
export class NewLectComponent implements OnInit {
  @Output() opendata: EventEmitter<null> = new EventEmitter();
  @Input("data") data: lect;
  clients: { title: string, value: string }[] = [];
  ngOnInit() {
    if (!this.data) {
      this.data = {
        second: {
          result: []
        },
        thired: {
          result: []
        }
      }
    }
    if (!this.data.second) this.data.second = {
      result: []
    };
    if (!this.data.thired) this.data.thired = {
      result: []
    };
    this.getClients()
  }
  constructor(private app: AppService) { }
  insertNewLect() {
    this.app.wait();
    if (this.data != undefined) {
      this.app.http.put(`api/lects/${this.app.user.id}/${this.data.clientId}/${this.data.id}`, this.data).subscribe(
        res => {
          this.app.wait(false);
          this.opendata.emit();
        }, err => { }
      );
    } else {
      this.app.http.post(`api/lects/${this.app.user.id}/${this.data.clientId}`, this.data).subscribe(
        res => {
          this.app.wait(false);
          this.opendata.emit();
        }, err => { }
      );
    }
  }
  async getClients() {
    this.app.http.get(`api/clients/${this.app.user.id}/all`).subscribe(
      (res: any) => {
        this.clients = res;
      }, err => { }
    )
  }
}
